body {
  text-align: center;
  background-color: #282c34;
  color: white;
  margin: 50px;
}

h1 {
  margin: 0;
  font-size: 4em;
}

#slogan {
  margin: 0;
  font-size: 2em;
}

button {
  background: black;
  color: white;
  border: 0;
  font-size: 1.5em;
  padding: 12px;
  cursor: pointer;
  margin-top: 30px;
}

#calendar {
  background: white;
  color: black;
  font-size: 24px;
  padding: 20px;
  margin: 20px;
  height: 600px;
}

#admin {
  background: white;
  padding-left: 50px;
  padding-right: 50px;
  padding-top: 20px;
  padding-bottom: 30px;
  margin: 20px;
  color: black;
}

#admin-btn {
  margin-top: 30px;
}
